import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader'
import { useLoader } from '@react-three/fiber'

export default function GedeStudio() {
    const gltf = useLoader(
        GLTFLoader,
        'https://cdn.jsdelivr.net/gh/gedewir/gdaygede-models/Gede-Studio/Combined/Gede-Room.glb'
        )  
        return(
            <>
                <primitive 
                    object={gltf.scene}
                    scale={1}
                    position={[-1.5,0,0]}
                />
            </>
        ) 
}