import { useEffect } from 'react';
import { Canvas } from "@react-three/fiber";
import { Stats, Environment, OrbitControls } from "@react-three/drei";
import GedeStudio from "./models/GedeStudio";

const GedeStudioCanvas = () => {
    return (
        <div id="canvas-container" style={{ width: "80vw", height: "80vh", margin: "0 auto" }}>
            <Canvas camera={{ position: [3, 5, 5], fov:80 }}>
                <OrbitControls
                    minAzimuthAngle={-Math.PI / 8}
                    maxAzimuthAngle={Math.PI / 2}
                    minPolarAngle={Math.PI / 3}
                    maxPolarAngle={Math.PI - Math.PI / 1.6}
                    minDistance={7}
                    maxDistance={7}
                />
                <ambientLight />
                <GedeStudio />
                <directionalLight position={[3.3, 1.0, 4.4]} />
                <axesHelper args={[5]} />
                {/* <Stats /> */}
            </Canvas>
        </div>
    );
};

export default GedeStudioCanvas;
